<template>
  <div class="text-center show-dialog__charla">
    <v-dialog
      v-model="value.active"
      max-width="1400"
      persistent
      scrollable
    >
      <validation-observer
        ref="observer"
        v-slot="{ invalid }"
      >
        <form @submit.prevent="validar()">
          <v-card class="dialog-charla-card">
            <v-card-title class="dialog-charla-titulo text-h5">
              <h3>
                {{ value.charla.CodCharla ? `Editando: ` : 'Nueva charla' }}
              </h3>
              <span
                v-if="value.charla.Titulo"
                v-html="value.charla.Titulo"
              />
            </v-card-title>
            <v-card-text>
              <v-row>
                <v-col class="col-12">
                  <v-row class="row-validations">
                    <v-col class="col-12">
                      <v-row class="row-validations__title">
                        <v-col class="main">
                          <h3>Información</h3>
                        </v-col>
                      </v-row>
                      <v-row class="row-validations__content">
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:100"
                            name="Título"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Título
                                </label>
                              </template>
                              <span>Título de la charla</span>
                            </v-tooltip>
                            <v-text-field
                              v-model="value.charla.Titulo"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:250"
                            name="Meta Title"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Meta Title
                                </label>
                              </template>
                              <span>Título a mostrar en pestaña (considerar
                                SEO)</span>
                            </v-tooltip>

                            <v-text-field
                              v-model="value.charla.MetaTitle"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:250"
                            name="Hora"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Hora
                                </label>
                              </template>
                              <span>Hora en que comenzará la charla</span>
                            </v-tooltip>
                            <v-menu
                              ref="menu"
                              v-model="timePicker"
                              :close-on-content-click="false"
                              :nudge-right="40"
                              :return-value.sync="value.charla.Hora"
                              transition="scale-transition"
                              offset-y
                              max-width="290px"
                              min-width="290px"
                            >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="value.charla.Hora"
                                  label="Seleccione una hora"
                                  prepend-icon="mdi-clock-time-four-outline"
                                  readonly
                                  v-bind="attrs"
                                  filled
                                  color="info"
                                  v-on="on"
                                />
                              </template>
                              <v-time-picker
                                v-if="timePicker"
                                v-model="value.charla.Hora"
                                :error-messages="errors"
                                full-width
                                color="info"
                                format="24hr"
                                @click:minute="
                                  $refs.menu.save(value.charla.Hora)
                                "
                              />
                            </v-menu>
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:250"
                            name="Hora"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Fecha
                                </label>
                              </template>
                              <span>Día en que se dictará la charla</span>
                            </v-tooltip>
                            <v-menu
                              v-model="datePicker"
                              :close-on-content-click="false"
                              :nudge-right="40"
                              transition="scale-transition"
                              offset-y
                              min-width="auto"
                            >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                  v-model="value.charla.Fecha"
                                  label="Seleccione una fecha"
                                  prepend-icon="mdi-calendar"
                                  readonly
                                  filled
                                  color="info"
                                  v-bind="attrs"
                                  v-on="on"
                                />
                              </template>
                              <v-date-picker
                                v-model="value.charla.Fecha"
                                :error-messages="errors"
                                color="info"
                                @input="datePicker = false"
                              />
                            </v-menu>
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:100"
                            name="Sucursal"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Sucursal
                                </label>
                              </template>
                              <span>Sucursal que organizará la charla</span>
                            </v-tooltip>
                            <v-select
                              v-model="value.charla.Sucursal"
                              :items="sucursales"
                              :error-messages="errors"
                              filled
                              item-text="Nombre"
                              item-value="CodSucursal"
                              label="Sucursal"
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:100"
                            name="En menú"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  En menú
                                </label>
                              </template>
                              <span>Especifica si la charla estará visible en el
                                menú del sitio</span>
                            </v-tooltip>
                            <v-select
                              v-model="value.charla.InMenu"
                              :items="[
                                { id: 1, desc: 'Sí' },
                                { id: 0, desc: 'No' },
                              ]"
                              :error-messages="errors"
                              filled
                              item-text="desc"
                              item-value="id"
                              color="info"
                              label="Sucursal"
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            name="Imagen"
                            rules="ext:jpg,png|size:1000"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text label-charla"
                                  v-on="on"
                                  v-html="
                                    value.charla.Imagen
                                      ? `Cambiar Imagen (Ver imagen actual: <a style='color: orange !important;' href='https://res.cloudinary.com/dkod5pdlc/image/upload/v${value.charla.VersionImagen}/terapiacl/charlas-y-talleres/charlas-imagenes-principales/${value.charla.Imagen}' target='_blank'>${value.charla.Imagen}</a>)`
                                      : 'Subir Imagen'
                                  "
                                />
                              </template>
                              <span>Cambia la imagen actual asociada a la
                                charla</span>
                            </v-tooltip>
                            <v-file-input
                              v-model="value.imagen"
                              small-chips
                              :error-messages="errors"
                              label="Formato: jpg, (1200x400px sugeridos)"
                              dense
                              filled
                              color="info"
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            name="Imagen Miniatura"
                            rules="ext:jpg,png|size:1000"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text label-charla"
                                  v-on="on"
                                  v-html="
                                    value.charla.ImagenMiniatura
                                      ? `Cambiar Imagen Miniatura (Ver imagen actual: <a style='color: orange !important;' href='https://res.cloudinary.com/dkod5pdlc/image/upload/v${value.charla.VersionImagen}/terapiacl/charlas-y-talleres/charlas-imagenes-miniaturas/${value.charla.Imagen}' target='_blank'>${value.charla.Imagen}</a>)`
                                      : 'Subir Imagen Miniatura'
                                  "
                                />
                              </template>
                              <span>Cambia la Imagen en miniatura actual asociada a
                                la charla (la que se muestra en el listado).
                                Debe ser una imagen cuadrada, idealmente de
                                400x400px</span>
                            </v-tooltip>
                            <v-file-input
                              v-model="value.imagenMiniatura"
                              small-chips
                              :error-messages="errors"
                              label="Formato: jpg, (400x400px sugeridos)"
                              dense
                              filled
                              color="info"
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:100"
                            name="Relator"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Relator
                                </label>
                              </template>
                              <span>Datos del relator que dictará la charla</span>
                            </v-tooltip>

                            <v-text-field
                              v-model="value.charla.Relator"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:255"
                            name="Dirigido"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Dirigido
                                </label>
                              </template>
                              <span>Segmento al que está dirigida la charla</span>
                            </v-tooltip>

                            <v-text-field
                              v-model="value.charla.Dirigido"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:100"
                            name="Duración"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Duración
                                </label>
                              </template>
                              <span>Tiempo cronológico que durará la charla</span>
                            </v-tooltip>

                            <v-text-field
                              v-model="value.charla.Duracion"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:50"
                            name="Enlace"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Enlace (formato: nueva-charla-providencia)
                                </label>
                              </template>
                              <span>URL que tendrá la charla al abrirla en
                                navegador</span>
                            </v-tooltip>

                            <v-text-field
                              v-model="value.charla.ToLink"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:20"
                            name="Valor"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Valor
                                </label>
                              </template>
                              <span>Valor que tendrá la charla</span>
                            </v-tooltip>

                            <v-text-field
                              v-model="value.charla.Valor"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:50"
                            name="Lugar"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Lugar
                                </label>
                              </template>
                              <span>Lugar donde se dictará la charla</span>
                            </v-tooltip>

                            <v-text-field
                              v-model="value.charla.Lugar"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12 col-md-6">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:50"
                            name="Modalidad"
                          >
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <label
                                  v-bind="attrs"
                                  class="grey--text"
                                  v-on="on"
                                >
                                  Modalidad
                                </label>
                              </template>
                              <span>Modalidad (charla, taller, etc...)</span>
                            </v-tooltip>

                            <v-text-field
                              v-model="value.charla.Modalidad"
                              :error-messages="errors"
                              color="info"
                              type="text"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                        <v-col class="col-12">
                          <h4>Introducción</h4>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:5000"
                            name="Introducción"
                          >
                            <vue-editor
                              v-model="value.charla.Introduccion"
                              :error-messages="errors"
                              :editor-toolbar="editorToolbarOptions"
                            />
                          </validation-provider>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                  <v-row class="row-breakdown-items">
                    <v-col>
                      <v-row class="row-breakdown-items__title">
                        <v-col class="main">
                          <h3>
                            Meta Tags
                            <v-icon dark>
                              mdi-tag-plus
                            </v-icon>
                          </h3>
                          <v-btn
                            color="info"
                            :small="$vuetify.breakpoint.xs"
                            @click="abrirCrearMetaTag()"
                          >
                            <v-icon>
                              mdi-plus-box
                            </v-icon>
                            Crear Nuevo MetaTag
                          </v-btn>
                        </v-col>
                      </v-row>
                      <v-row class="row-breakdown-items__content">
                        <v-col class="col-12">
                          <v-row class="row-breakdown-items__form">
                            <v-col class="col-12 col-md-6 inputs">
                              <v-text-field
                                v-model="value.metaTagBox.name"
                                label="Name"
                                solo
                              />
                              <v-text-field
                                v-model="value.metaTagBox.content"
                                label="Content"
                                solo
                              />
                              <v-btn
                                v-if="value.crear"
                                color="info"
                                @click="crearMetaTag()"
                              >
                                Crear
                              </v-btn>
                              <v-btn
                                v-else
                                @click="editarMetaTag()"
                              >
                                editar
                              </v-btn>
                            </v-col>
                            <v-col class="col-12 col-md-6 alerts">
                              <div class="alert-main">
                                <v-alert
                                  v-if="showMsg"
                                  dense
                                  text
                                  :type="typeMsg"
                                >
                                  <span v-html="msg" />
                                </v-alert>
                              </div>
                            </v-col>
                          </v-row>
                          <v-row class="row-breakdown-items__table justify-center">
                            <v-col class="col-12">
                              <div class="divider" />
                            </v-col>
                            <v-col
                              v-if="
                                value.charla.MetaTags != undefined &&
                                  value.charla.MetaTags.length > 0
                              "
                              class="col-12 col-md-9 mt-5 mb-5 table-main"
                            >
                              <v-simple-table dense>
                                <template v-slot:default>
                                  <thead>
                                    <tr>
                                      <th class="text-left">
                                        Name
                                      </th>
                                      <th class="text-left">
                                        Content
                                      </th>
                                      <th>
                                        Editar
                                      </th>
                                      <th>
                                        Eliminar
                                      </th>
                                    </tr>
                                  </thead>
                                  <tbody>
                                    <tr
                                      v-for="(item, i) in value.charla.MetaTags"
                                      :key="i"
                                    >
                                      <td>{{ item.Name }}</td>
                                      <td>{{ item.Content }}</td>
                                      <td class="td-btn">
                                        <v-btn
                                          color="info"
                                          small
                                          @click="
                                            abrirEditarMetaTag(
                                              value.charla.MetaTags.indexOf(
                                                item
                                              )
                                            )
                                          "
                                        >
                                          <v-icon>mdi-pencil</v-icon>
                                        </v-btn>
                                      </td>
                                      <td class="td-btn">
                                        <v-btn
                                          color="info"
                                          small
                                          @click="
                                            eliminarMetaTag(
                                              value.charla.MetaTags.indexOf(
                                                item
                                              )
                                            )
                                          "
                                        >
                                          <v-icon>mdi-delete</v-icon>
                                        </v-btn>
                                      </td>
                                    </tr>
                                  </tbody>
                                </template>
                              </v-simple-table>
                            </v-col>
                            <v-col
                              v-else
                              class="no-breakdown-items"
                            >
                              Aún no has añadido metatags a esta charla
                            </v-col>
                          </v-row>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                  <v-row class="row-breakdown-items">
                    <v-col>
                      <v-row class="row-breakdown-items__title">
                        <v-col class="main">
                          <h3>
                            Objetivos
                            <v-icon dark>
                              mdi-bullseye-arrow
                            </v-icon>
                          </h3>
                        </v-col>
                      </v-row>
                      <v-row class="row-breakdown-items__content">
                        <v-col class="col-12">
                          <v-row class="row-breakdown-items__form">
                            <v-col class="col-12 inputs">
                              <v-checkbox
                                v-for="(item, i) in value.objetivos"
                                :key="i"
                                v-model="item.checked"
                                :label="item.Descripcion"
                                color="info"
                                hide-details
                              />
                            </v-col>
                          </v-row>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                  <v-row class="row-breakdown-items">
                    <v-col>
                      <v-row class="row-breakdown-items__title">
                        <v-col class="main">
                          <h3>
                            Temas
                            <v-icon dark>
                              mdi-clipboard-text-multiple
                            </v-icon>
                          </h3>
                        </v-col>
                      </v-row>
                      <v-row class="row-breakdown-items__content">
                        <v-col class="col-12">
                          <v-row class="row-breakdown-items__form">
                            <v-col class="col-12 inputs">
                              <v-checkbox
                                v-for="(item, i) in value.temas"
                                :key="i"
                                v-model="item.checked"
                                :label="item.Descripcion"
                                color="info"
                                hide-details
                              />
                            </v-col>
                          </v-row>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                color="darken-1"
                text
                @click="cerrar()"
              >
                Cerrar
              </v-btn>
              <v-btn
                color="info"
                default
                type="submit"
                :disabled="invalid"
                :loading="value.loader"
              >
                Grabar
              </v-btn>
            </v-card-actions>
          </v-card>
        </form>
      </validation-observer>
    </v-dialog>
  </div>
</template>

<script>
// import Vue from 'vue'
  import { VueEditor } from 'vue2-editor'
  import { mapState } from 'vuex'
  export default {
    name: 'ShowDialogCharla',
    components: {
      VueEditor,
    },
    props: {
      value: {
        active: {
          type: Boolean,
          default: false,
        },
        charla: {
          type: Object,
          default: false,
        },
        imagen: {
          type: Object,
          default: false,
        },
        imagenMiniatura: {
          type: Object,
          default: false,
        },
        loader: {
          type: Boolean,
          default: false,
        },
        metaTagBox: {
          type: Object,
          default: {},
          name: {
            type: String,
            default: '',
          },
          content: {
            type: String,
            default: '',
          },
          crear: {
            type: Boolean,
            default: true,
          },
        },
        objetivos: {
          type: Array,
          default: false,
        },
        default: null,
        type: Object,
      },
    },
    data () {
      return {
        editorToolbarOptions: require('@/data/editorToolbarOptions.json'),
        currentIndex: false,
        msg: '',
        showMsg: false,
        typeMsg: false,
        timePicker: false,
        datePicker: false,
        ex4: [],
      }
    },
    computed: {
      ...mapState('web', ['sucursales']),
    },
    watch: {
      'value.active': function (value) {
        requestAnimationFrame(() => {
          // Reseteamos vee al cerrar dialog
          this.$refs.observer.reset()
        })
      },
    },
    methods: {
      validar () {
        this.$emit('procesar', this.value)
      },
      cerrar () {
        this.$emit('cerrar')
      },
      abrirEditarMetaTag (index) {
        // console.log(`abrirEditar MetaTag ${index}`)
        this.currentIndex = index
        this.value.crear = false
        this.value.metaTagBox.name = this.value.charla.MetaTags[index].Name
        this.value.metaTagBox.content = this.value.charla.MetaTags[index].Content
      },
      abrirCrearMetaTag () {
        this.value.crear = true
        this.value.metaTagBox = {
          name: '',
          content: '',
        }
      },
      crearMetaTag () {
        const element = {
          Name: this.value.metaTagBox.name,
          Content: this.value.metaTagBox.content,
        }
        if (!this.value.charla.MetaTags) this.value.charla.MetaTags = []
        this.value.charla.MetaTags.unshift(element)
        this.limpiaMetaTagBox()
        this.showAlert(
          'Registro creado. Debe grabar para guardar cambios definitivamente',
          'success',
        )
      },
      async editarMetaTag () {
        this.value.charla.MetaTags[
          this.currentIndex
        ].Content = this.value.metaTagBox.content
        this.value.charla.MetaTags[
          this.currentIndex
        ].Name = this.value.metaTagBox.name
        console.log(this.limpiaMetaTagBox())
        this.limpiaMetaTagBox()
        this.showAlert(
          'Registro editado. Debe grabar para guardar cambios definitivamente',
          'success',
        )
      },
      eliminarMetaTag (index) {
        if (confirm('¿Estás seguro/a que deseas eliminar este MetaTag?')) {
          this.value.charla.MetaTags.splice(index, 1)
          if (this.value.charla.MetaTags.length === 0) {
            this.value.charla.MetaTags = 0
          }
          this.showAlert(
            'Registro eliminado. Debe grabar para guardar cambios definitivamente',
            'warning',
          )
        }
      },
      limpiaMetaTagBox () {
        this.value.crear = true
        this.value.metaTagBox.name = ''
        this.value.metaTagBox.content = ''
        this.currentIndex = false
      },
      showAlert (msg, typeMsg) {
        this.msg = msg
        this.typeMsg = typeMsg
        this.showMsg = true
        setTimeout(() => {
          this.msg = ''
          this.typeMsg = ''
          this.showMsg = false
        }, 6000)
      },
    },
  }
</script>

